@media screen and (max-width: 964px) { 
    .hd-text__do-it {
        display: none;
    }

    .hd-text__user-count {
        display: none !important;
    }

    html {
        font-size: 12px;
    }
    .hd-button-group {
        text-align: right;
    }
    .narrow-line-height span {
        line-height: 1 !important;
    }
}

.hd-button-group {
    text-align: right;
}

.hd-text__do-it {
    margin-left: 0rem;
    font-size: 1.8rem;
    color: white;
    font-family: impact;
    transform: rotate(-3deg);
    margin-top: -10px;
    cursor: default;
}

.hd-text__user-count {
    color: #fff;
    /* font-size: 1.5rem; */
    cursor: default;
    text-align: center;
    flex-grow: 8;
    /* margin-left: -5vw; */
    display: flex;
    justify-content: center;
    align-items: center;
}


/* Overide */
.MuiButton-outlinedPrimary {
    color: #de2500;
    margin-right: 0.5rem;
}

.MuiButton-outlinedSecondary:hover {
    border: 1px solid #de2500 !important;
}

.MuiButton-outlined {
    border: 3px solid #de2500 !important;
}

/* Wallet Modal */
.gjZtyr, .bkldJX {
    font-family: 'Akira' !important;
    font-size: 18px !important;
    padding: 0 12px !important;
}

#wallet-connect-metamask, #wallet-connect-trustwallet, button[id='wallet-connect-coinbase wallet'],
#wallet-connect-tokenpocket, #wallet-connect-walletconnect,
button[id='wallet-connect-binance chain wallet'], button[id='wallet-connect-safepal wallet'] {
    font-family: 'Helvetica Neue,Segoe UI,Helvetica,Arial,sans-serif' !important;
    /* font-family: Kanit, sans-serif ; */
}

/* Send Tip Modal */
